	outline-offset: -2px; /* 1 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */

[type=search]::-webkit-search-decoration {
	-webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Fix font inheritance.
 */

::-webkit-file-upload-button {
	-webkit-appearance: button; /* 1 */
	font: inherit; /* 2 */
}

/**
 * Fix appearance for Firefox
 */

[type=number] {
	-moz-appearance: textfield;
}

/**
 * Clickable labels
 */

label[for] {
	cursor: pointer;
}

/* Interactive */

/* ============================================ */

/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */

details {
	display: block;
}

/*
 * Add the correct display in all browsers.
 */

summary {
	display: list-item;
}

/*
 * Remove outline for editable content.
 */

[contenteditable]:focus {
	outline: auto;
}

/* Tables */

/* ============================================ */

/**
1. Correct table border color inheritance in all Chrome and Safari.
*/

table {
	border-color: inherit; /* 1 */
	border-collapse: collapse;
}

caption {
	text-align: left;
}

td,
th {
	vertical-align: top;
	padding: 0;
}

th {
	text-align: left;
	font-weight: bold;
}

/* フェード設定 */

/* ============================================ */

.splide__container {
	box-sizing: border-box;
	position: relative;
}

.splide__list {
	backface-visibility: hidden;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	margin: 0 !important;
	padding: 0 !important;
}

.splide.is-initialized:not(.is-active) .splide__list {
	display: block;
}

.splide__pagination {
	-ms-flex-align: center;
	align-items: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	-ms-flex-pack: center;
	justify-content: center;
	margin: 0;
	pointer-events: none;
}

.splide__pagination li {
	display: inline-block;
	line-height: 1;
	list-style-type: none;
	margin: 0;
	pointer-events: auto;
}

.splide:not(.is-overflow) .splide__pagination {
	display: none;
}

.splide__progress__bar {
	width: 0;
}

.splide {
	position: relative;
	visibility: hidden;
}

.splide.is-initialized,
.splide.is-rendered {
	visibility: visible;
}

.splide__slide {
	backface-visibility: hidden;
	box-sizing: border-box;
	-ms-flex-negative: 0;
	flex-shrink: 0;
	list-style-type: none !important;
	margin: 0;
	position: relative;
}

.splide__slide img {
	vertical-align: bottom;
}

.splide__spinner {
	animation: splide-loading 1s linear infinite;
	border: 2px solid #999;
	border-left-color: transparent;
	border-radius: 50%;
	bottom: 0;
	contain: strict;
	display: inline-block;
	height: 20px;
	left: 0;
	margin: auto;
	position: absolute;
	right: 0;
	top: 0;
	width: 20px;
}

.splide__sr {
	clip: rect(0 0 0 0);
	border: 0;
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

.splide__toggle.is-active .splide__toggle__play,
.splide__toggle__pause {
	display: none;
}

.splide__toggle.is-active .splide__toggle__pause {
	display: inline;
}

.splide__track {
	overflow: hidden;
	position: relative;
	z-index: 0;
}

.splide__track--draggable {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.splide__track--fade > .splide__list > .splide__slide {
	margin: 0 !important;
	opacity: 0;
	z-index: 0;
}

.splide__track--fade > .splide__list > .splide__slide.is-active {
	opacity: 1;
	z-index: 1;
}

.splide--rtl {
	direction: rtl;
}

.splide__track--ttb > .splide__list {
	display: block;
}

.splide__arrow {
	-ms-flex-align: center;
	align-items: center;
	background: #ccc;
	border: 0;
	border-radius: 50%;
	cursor: pointer;
	display: -ms-flexbox;
	display: flex;
	height: 2em;
	-ms-flex-pack: center;
	justify-content: center;
	opacity: 0.7;
	padding: 0;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	width: 2em;
	z-index: 1;
}

.splide__arrow svg {
	fill: #000;
	height: 1.2em;
	width: 1.2em;
}

.splide__arrow:hover:not(:disabled) {
	opacity: 0.9;
}

.splide__arrow:disabled {
	opacity: 0.3;
}

.splide__arrow:focus-visible {
	outline: 3px solid #0bf;
	outline-offset: 3px;
}

.splide__arrow--prev {
	left: 1em;
}

.splide__arrow--prev svg {
	transform: scaleX(-1);
}

.splide__arrow--next {
	right: 1em;
}

.splide.is-focus-in .splide__arrow:focus {
	outline: 3px solid #0bf;
	outline-offset: 3px;
}

.splide__pagination {
	bottom: 0.5em;
	left: 0;
	padding: 0 1em;
	position: absolute;
	right: 0;
	z-index: 1;
}

.splide__pagination__page {
	background: #ccc;
	border: 0;
	border-radius: 50%;
	display: inline-block;
	height: 8px;
	margin: 3px;
	opacity: 0.7;
	padding: 0;
	position: relative;
	transition: transform 0.2s linear;
	width: 8px;
}

.splide__pagination__page.is-active {
	background: #fff;
	transform: scale(1.4);
	z-index: 1;
}

.splide__pagination__page:hover {
	cursor: pointer;
	opacity: 0.9;
}

.splide__pagination__page:focus-visible {
	outline: 3px solid #0bf;
	outline-offset: 3px;
}

.splide.is-focus-in .splide__pagination__page:focus {
	outline: 3px solid #0bf;
	outline-offset: 3px;
}

.splide__progress__bar {
	background: #ccc;
	height: 3px;
}

.splide__slide {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.splide__slide:focus {
	outline: 0;
}

.splide__toggle {
	cursor: pointer;
}

.splide__toggle:focus-visible {
	outline: 3px solid #0bf;
	outline-offset: 3px;
}

.splide.is-focus-in .splide__toggle:focus {
	outline: 3px solid #0bf;
	outline-offset: 3px;
}

.splide__track--nav > .splide__list > .splide__slide {
	border: 3px solid transparent;
	cursor: pointer;
}

.splide__track--nav > .splide__list > .splide__slide.is-active {
	border: 3px solid #000;
}

.splide__arrows--rtl .splide__arrow--prev {
	left: auto;
	right: 1em;
}

.splide__arrows--rtl .splide__arrow--prev svg {
	transform: scaleX(1);
}

.splide__arrows--rtl .splide__arrow--next {
	left: 1em;
	right: auto;
}

.splide__arrows--rtl .splide__arrow--next svg {
	transform: scaleX(-1);
}

.splide__arrows--ttb .splide__arrow {
	left: 50%;
	transform: translate(-50%);
}

.splide__arrows--ttb .splide__arrow--prev {
	top: 1em;
}

.splide__arrows--ttb .splide__arrow--prev svg {
	transform: rotate(-90deg);
}

.splide__arrows--ttb .splide__arrow--next {
	bottom: 1em;
	top: auto;
}

.splide__arrows--ttb .splide__arrow--next svg {
	transform: rotate(90deg);
}

.splide__pagination--ttb {
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	left: auto;
	padding: 1em 0;
	right: 0.5em;
	top: 0;
}

.l-header {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 50;
}

.l-inner {
	margin: 0 auto;
	width: 100%;
	height: inherit;
	max-width: calc(1200px + 6.6666%);
	padding-right: 3.3333%;
	padding-left: 3.3333%;
}

.l-scroll-top {
	position: fixed;
	bottom: 0.625rem;
	right: 0.625rem;
}

.l-section {
	margin-block: 2.8125rem;
}

.c-accordion {
	border: 0.125rem solid #1A1A1A;
}

.c-accordion summary::-webkit-details-marker {
	display: none;
}

.c-accordion__title {
	padding: 1.25rem 2.5rem;
	font-size: 1.125rem;
	background: #1A1A1A;
	color: #fff;
	text-decoration: none;
	cursor: pointer;
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
}

.c-accordion__icon i {
	transition: transform 0.3s;
	right: 0;
	position: relative;
	font-size: 1rem;
}

.c-accordion.is-open .c-accordion__icon i {
	transform: rotate(-180deg);
}

.c-accordion__content {
	font-size: 1.125rem;
	background: #fff;
	color: #1A1A1A;
	text-decoration: none;
	overflow: hidden;
}

.c-accordion__content-inner {
	padding: 1.25rem 2.5rem;
}

/* .c-accordion--type1 */

.c-accordion--type1 .c-accordion__title {
	background: #fff;
	color: #1A1A1A;
	transition: border 0.3s;
}

.c-accordion--type1 .c-accordion__icon i {
	font-size: 1.25rem;
}

/* .c-accordion--type2 */

.c-accordion--type2 .c-accordion__title {
	position: relative;
}

.c-accordion--type2 .c-accordion__icon::before,
.c-accordion--type2 .c-accordion__icon::after {
	content: "";
	position: absolute;
	top: 50%;
	right: 1.875rem;
	width: 0.9375rem;
	height: 0.125rem;
	background: #fff;
	transform: translate(-50%, -50%);
	transition: 0.3s all;
}

.c-accordion--type2 .c-accordion__icon::after {
	transform: translate(-50%, -50%) rotate(-90deg);
}

.c-accordion--type2.is-open .c-accordion__icon::after {
	transform: translate(-50%, -50%) rotate(0deg);
}

.c-applicationButton {
	width: 100%;
	max-width: 19rem;
	height: 4.5rem;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	border: 0.0625rem solid #023E78;
	color: #023E78;
	font-size: 1rem;
	font-weight: 500;
	line-height: normal;
	position: relative;
	transition: all 0.3s ease;
}

.c-applicationButton::before {
	content: "";
	position: absolute;
	width: 1.25rem;
	height: 0.875rem;
	background-image: url(../images/common/icon-arrow--blue_02.png);
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	right: 1.25rem;
	transition: all 0.3s ease;
}

.c-applicationButton:hover {
	background-color: #023E78;
	color: #fff;
}

.c-applicationButton:hover::before {
	background-image: url(../images/common/icon-arrow--white_02.png);
}

.c-button {
	display: inline-block;
	text-align: center;
	transition: 0.3s;
	padding: 1.25rem 2.5rem;
	max-width: 100%;
	font-size: 1.125rem;
	background: #1A1A1A;
	color: #fff;
	border: 0.0625rem solid #1A1A1A;
	text-decoration: none;
}

.c-button:hover {
	background: #fff;
	color: #1A1A1A;
}

.c-button--arrow {
	position: relative;
	padding-right: 3.125rem;
}

.c-button--arrow::after {
	content: "\f105";
	position: absolute;
	font-family: "Font Awesome 6 Free";
	font-weight: 900;
	top: 50%;
	right: 0.9375rem;
	transform: translateY(-50%);
	font-size: 1rem;
}

.c-card__body {
	padding: 0.9375rem;
}

.c-card__time {
	font-size: 0.875rem;
}

.c-card__title {
	font-weight: bold;
	font-size: 1.25rem;
	margin-top: 0.625rem;
}

.c-card__text {
	margin-top: 0.625rem;
}

.c-card--shadow {
	box-shadow: 0 0 0.3125rem 0 rgba(0, 0, 0, 0.3);
}

.c-card--horizon {
	display: grid;
	grid-template-columns: 20% 80%;
}

.c-card--horizon .c-card__body {
	padding: 0.625rem 0.9375rem;
}

.c-caseButton {
	padding: 1.5rem 5rem;
	display: inline-block;
	border: 0.0625rem solid #023E78;
	color: #023E78;
	font-size: 1rem;
	font-weight: 500;
	line-height: normal;
	position: relative;
	transition: all 0.3s ease;
}

.c-caseButton::before {
	content: "";
	position: absolute;
	width: 1.25rem;
	height: 0.875rem;
	background-image: url(../images/common/icon-arrow--blue_02.png);
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	right: 1.375rem;
	transition: all 0.3s ease;
	top: 50%;
	transform: translateY(-50%);
}

.c-caseButton:hover {
	background-color: #023E78;
	color: #fff;
}

.c-caseButton:hover::before {
	background-image: url(../images/common/icon-arrow--white_02.png);
}

.c-drawer__button {
	display: flex;
	height: 3.125rem;
	width: 3.125rem;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	transition: all 0.3s ease-in-out;
	box-shadow: 0 0 2rem transparent;
	position: relative;
	z-index: 200;
	background: #ccc;
}

.c-drawer__button:hover,
.c-drawer__button:focus {
	box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
	background: #1A1A1A;
}

.c-drawer__line {
	font-size: 0;
}

.c-drawer__line,
.c-drawer__line::before,
.c-drawer__line::after {
	content: "";
	display: block;
	height: 0.1875rem;
	width: 1.5625rem;
	border-radius: 0.1875rem;
	background-color: #fff;
	position: absolute;
	transition: all 0.2s ease-in-out;
}

.c-drawer__line::before {
	bottom: 0.5rem;
}

.c-drawer__line::after {
	top: 0.5rem;
}

.c-drawer__button[aria-expanded=true] {
	background: #1A1A1A;
}

.c-drawer__button[aria-expanded=true] .c-drawer__line {
	background-color: transparent;
}

.c-drawer__button[aria-expanded=true] .c-drawer__line::before {
	bottom: 0;
	transform: rotate(45deg);
}

.c-drawer__button[aria-expanded=true] .c-drawer__line::after {
	top: 0;
	transform: rotate(-45deg);
}

.c-drawer__modal {
	visibility: hidden;
	opacity: 0;
	transition: all 0.2s;
	z-index: -1;
	position: fixed;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	background: rgba(255, 255, 255, 0.95);
	display: block;
}

.c-drawer__modal.is-open {
	visibility: visible;
	opacity: 1;
	z-index: 100;
}

.c-drawer__lists {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.c-drawer__link {
	font-size: 1.25rem;
	font-weight: bold;
	display: block;
	color: #1A1A1A;
	padding-block: 1.25rem;
	text-align: center;
}

/* c-drawer--open-right */

.c-drawer--open-right .c-drawer__modal {
	transform: translateX(100%);
}

.c-drawer--open-right .c-drawer__modal.is-open {
	transform: translateX(0);
}

/* c-drawer--open-left */

.c-drawer--open-left .c-drawer__modal {
	transform: translateX(-100%);
}

.c-drawer--open-left .c-drawer__modal.is-open {
	transform: translateX(0);
}

/* c-drawer--open-top */

.c-drawer--open-top .c-drawer__modal {
	transform: translateY(-100%);
}

.c-drawer--open-top .c-drawer__modal.is-open {
	transform: translateY(0);
}

